.Botton_Styled{
    min-width: 127px;
    height: 33px;

    background-size: cover;
    border: none;
    background-position: center;
    background-color: transparent;
    
    background-image: url(../../Assets/Botton_background.png);
    background-repeat: no-repeat;
    cursor: pointer;

    font-size: 20px;
    text-decoration:solid;
    font-weight: 600;
    line-height: 5px;
}

.Botton_Styled_active{
    min-width: 127px;
    height: 33px;

    background-size: cover;
    border: none;
    background-position: center;
    background-color: transparent;
    
    background-image: url(../../Assets/Button_active.png);
    background-repeat: no-repeat;
    cursor: pointer;

    font-size: 20px;
    text-decoration:solid;
    font-weight: 600;
    line-height: 5px;
}

.Botton_Styled:disabled{
    opacity: 0.5;
    cursor:no-drop;
}

.Botton_Styled::selection{
    cursor: progress;
}